import React, { PureComponent } from "react";
import HeaderNavWraper from "./header-nav-components/header-nav-wraper";
import HeaderProduct from "./header-product";
import ConfigProcessor from "../../ConfigJsonProcessor";
import { BrowserRouter as Router} from "react-router-dom";
class HeaderWraper extends PureComponent {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  state = {};
  render() {
    
    let propsData = this.props.header;
    let navigation = {
      assets: propsData.assets,
      data: propsData.data,
      navigation: propsData.component.content.NavigationMenu
    };
    let productList = {
      assets: propsData.assets,
      data: propsData.data,
      product: propsData.component.content.ProductItemList
    };
    let banner = {
      assets: propsData.assets,
      img: propsData.component.content.Banner.gifBanner,
      data: propsData.data,
      banner: propsData.component.content.Banner.BannerContent
    };
    
    return (
      <header className="header-section">
        <HeaderNavWraper navigation={navigation} />
      <div className="container-fluid banner-section">
      <div className="row">
      <div className="col-md-7 left-banner d-flex justify-content-center flex-wrap">

      {/* <h1>Protection<br/> Made Easy</h1>
          <h2><span>What do you want to insure?</span><br/>Select a product to get a free quote</h2> */}

          <h1> {this.cfp.getDataByKey(
                  this.props.header.data[0].messages,
                  this.props.header.component.content.Banner.BannerContent[0].bannerTitle1
                )}<br/>
                {this.cfp.getDataByKey(
                  this.props.header.data[0].messages,
                  this.props.header.component.content.Banner.BannerContent[0].bannerTitle2
                )} {this.cfp.getDataByKey(
                  this.props.header.data[0].messages,
                  this.props.header.component.content.Banner.BannerContent[0].bannerTitle3
                )}</h1>

            <h2><span>{this.cfp.getDataByKey(
              this.props.header.data[0].messages,
              this.props.header.component.content.Banner.BannerContent[0].bannerCaption1
            )}</span><br/>
            {this.cfp.getDataByKey(
              this.props.header.data[0].messages,
              this.props.header.component.content.Banner.BannerContent[0].bannerCaption2
            )}
            </h2>


          <div className="banner-links d-flex w-100">
              {/* <a href="#"></a> */}
              <Router><React.Fragment>
              {/* <Link to="/quote"><img src="images/travel-icon.svg"></img>Travel</Link> */}
          
            {/* <Route  path="/quote" component={App.page()} /> */}
         </React.Fragment>
        </Router>
              {/* <a href="#"><img src="images/motor-icon.svg"></img>Motor</a>
              <a href="#"><img src="images/pa-icon.svg"></img>PA</a>
              <a href="#"><img src="images/dental-icon.svg"></img>Dental</a>
              <a href="#"><img src="images/guard-icon.svg"></img>Guard</a>
              <a href="#"><img src="images/ride-icon.svg"></img>Ride</a> */}

              <HeaderProduct product={productList} />
          </div>
          
      </div>
      <div className="col-md-5 right-banner mt-5 mt-md-0 mt-xl-0 mt-xxl-0">
      {/* <img class="banner-logo" src="images/Travel.gif"/> */}
      <img className="banner-logo" alt="bannerGif" src={this.cfp.getAssetUrl(
        banner.assets,
        banner.img
      )}/>
      </div>
      
      </div>
      </div>
      </header>
    );
  }
}

export default HeaderWraper;



