import React, { Component } from "react";
import ConfigProcessor from "../ConfigJsonProcessor";
class Protection extends Component {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  state = {};
  itemWraper() {
    if (
      this.props.protectionElement.component.content.hasOwnProperty(
        "ListImgs"
      ) &&
      this.props.protectionElement.component.content.ListImgs
    ) {
      const list = this.props.protectionElement.component.content.ListImgs.map(
        component => {
          return (
            <li className="policy-item">
              <a href={component.url}>
                <img
                  className="img-fluid"
                  src={this.cfp.getAssetUrl(
                    this.props.protectionElement.assets,
                    component.assetId
                  )}
                  alt=""
                />
              </a>
            </li>
          );
        }
      );
      return <React.Fragment>{list}</React.Fragment>;
    }
  }
  render() {
    return (
      <div className="policy">
        <div className="container protect-section">
          <div className="row text-center">
            <div className="col-12 title">
              <h3 className="display-5">
                {this.cfp.getDataByKey(
                  this.props.protectionElement.data[0].messages,
                  this.props.protectionElement.component.content.caption
                )}
              </h3>
              <h4 className="display-5">
                {this.cfp.getDataByKey(
                  this.props.protectionElement.data[0].messages,
                  this.props.protectionElement.component.content.subCaption
                )}
              </h4>
            </div>
          </div>
          <div className="row flex justify-content-center">
            <div className="col-xs-12 col-sm-6 col-md-4 text-sm-right flex policy-img">
              <img
                className="img-fluid"
                src={this.cfp.getAssetUrl(
                  this.props.protectionElement.assets,
                  this.props.protectionElement.component.content.pro_themeImg
                )}
                alt=""
              />
            </div>
            <div className="col-xs-12 col-sm-6 col-md-4 text-sm-left protect-margin">
              {/* <ul className="policy-list list-inline">{this.itemWraper()}</ul> */}
              {/* <img className="image-ad-size" src="images/apple.svg"></img><br/>
              <img className="image-ad-size" src="images/google-play.svg"></img> */}
              <img className="image-ad-size" alt="appleStoreImg" src={this.cfp.getAssetUrl(
                  this.props.protectionElement.assets,
                  this.props.protectionElement.component.content.downloadAppleStoreImg
                )}></img><br/>
              <img className="image-ad-size" alt="playStoreImg" src={this.cfp.getAssetUrl(
                  this.props.protectionElement.assets,
                  this.props.protectionElement.component.content.downloadPlayStoreImg
                )}></img>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Protection;



