import React, { Component } from "react";
import "./App.css";
import ConfigJSON from "./ConfigJSON";
import ComponetBuilder from "./components-toolset/component-builder";

import { BrowserRouter as Router, Route } from "react-router-dom";
let firstTime = true;
class App extends Component {
  constructor() {
    super();
    this.state = { obj: {} };
    this.configjson = new ConfigJSON();
  }
  builder() {
    return (
      <React.Fragment>
        <Router>
          <div>
            <Route exact path="/:pageId" component={this.page} />
          </div>
        </Router>
      </React.Fragment>
    );
  }
  page = ({ match }) => {
    return <div>{this.componentWraper(match.params.pageId)}</div>;
  };

  componentWraper(pageId) {
    let inputJson = this.configjson.subcribe(pageId);
    console.log(JSON.stringify("Strig=>>>>>>>>>>>>>>>",inputJson));
    
    inputJson
      .then(response => {
        const dataParsed = response && response.data;
        this.setState({ obj: dataParsed });
      })
      .catch(e => {
        console.log(e);
      });
  }
  render() {
    const { obj } = this.state;
    return Object.keys(obj).length > 0 ? (
      <ComponetBuilder testingState={obj} />
    ) : (
      this.builder()
    );
  }
}

export default App;



