import React, { PureComponent } from "react";
import ConfigProcessor from "../../../ConfigJsonProcessor";
class HeaderNavLeft extends PureComponent {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  state = {};
  itemWraperMenuRight() {
    if (
      this.props.menubar.hasOwnProperty("navigation") &&
      this.props.menubar.navigation
    ) {
      let list = this.props.menubar.navigation.menuItem.map((component, index) => {
        return (
          <React.Fragment key={index}>
          <li className="nav-item">
            <a className="nav-link nav-link-common" href="link">
              {this.cfp.getDataByKey(
                this.props.menubar.data[0].messages,
                component
              )}
            </a>
          </li>
          <li>
            
          </li>
          </React.Fragment>
        );
      });
      return <React.Fragment>{list}</React.Fragment>;
    }
  }
  itemWraperSystemOptions() {
    if (
      this.props.menubar.hasOwnProperty("navigation") &&
      this.props.menubar.navigation
    ) {
      if (
        this.props.menubar.navigation.hasOwnProperty("systemOptions") &&
        this.props.menubar.navigation.systemOptions
      ) {
        let list = this.props.menubar.navigation.systemOptions.map(
          (component, index) => {
            return (
              <li key={index} className="nav-item">
                <a className="nav-link" href="link">
                  {this.cfp.getDataByKey(
                    this.props.menubar.data[0].messages,
                    component
                  )}
                </a>
              </li>

              
            );
          }
        );
        return <React.Fragment>{list}</React.Fragment>;
      }
    }
  }
  render() {
    console.log("get started button&&&&&&&&&&&&&&", this.props.menubar.navigation.searchBtn)
    return (
      <div className="collapse navbar-collapse main-nav" id="navbarResponsive">
        <ul className="navbar-nav mr-auto">{this.itemWraperMenuRight()}
        
        <a className="navbar-nav mr-auto nav-link">{this.cfp.getDataByKey(
          this.props.menubar.data[0].messages,
          this.props.menubar.navigation.downloadApp 
        )}</a>
        </ul>

        {/* <img src={this.cfp.getAssetUrl(
                this.props.menubar.assets,
                this.props.menubar.navigation.searchBtn
              )}/> */}
        
        
        <ul className="navbar-nav">
          {this.itemWraperSystemOptions()}
          {/* <button class="button-started">Get Started</button> */}
          <button className="button-started">{this.cfp.getDataByKey(
                this.props.menubar.data[0].messages,
                this.props.menubar.navigation.getStartedBtn
              )}</button> 
          <li className="nav-item">
            <a className="nav-link" href="link">
              {/* <img src="images/search-red.svg" /> */}
              <img alt="searchBtnLogo" src={this.cfp.getAssetUrl(
                this.props.menubar.assets,
                this.props.menubar.navigation.searchBtn
              )}/>
            </a>
          </li>
        </ul>
      </div>
    );
  }
}

export default HeaderNavLeft;



