import { PureComponent } from "react";
// import ConfigJSON from "./home-ce";
class ConfigJsonProcessor extends PureComponent {
  state = {};
  getAssetUrl(assetsdata, imgName) {
    //const imgName = ConfigJSON.components[2].components[0].content.image;
    const assets = assetsdata;
    let url = "";
    assets.map(asset =>  {
      if (asset.assetId === imgName) url = asset.assetUrl;
    });
    return url;
  }
  getDataByKey(Dataset, dataKey) {
    const datas = Dataset;
    let value = "";
    datas.map(data => {
      if (data.key === dataKey) value = data.value;
    });
    return value;
  }
  getDataByIconKey(Dataset, dataKey) {
    const datas = Dataset;
    let value = "";
    datas.map(data => {
      if (data.key === dataKey.icon) value = data.value;
    });
    return value;
  }
}

export default ConfigJsonProcessor;



