import React from "react";
import TemplateMaper from "./template-maper";

const ComponentBuilder = props => {
  function template(inputJSON) {
    const rows = [];
    const templatemaper = new TemplateMaper();
    let components = inputJSON.components;
    components.forEach(element => {
      let data = {
        component: element,
        assets: inputJSON.assets,
        data: [...inputJSON.data]
      };
      rows.push(templatemaper.matchTemplate(data));
    });
    return <React.Fragment>{rows}</React.Fragment>;
  }
  function build(inputJSON) {
    return <React.Fragment>{template(inputJSON)}</React.Fragment>;
  }

  return build(props.testingState);
};

export default ComponentBuilder;



