import React from "react";
import { Col, Container, Row, Footer } from "mdbreact";
import ConfigProcessor from "../ConfigJsonProcessor";

class FooterPage extends React.Component {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  blog() {
    let list = null;
    let componentdata = this.props.footerElement.component.content;
    if (
      componentdata.hasOwnProperty("colRow1Cat2") &&
      componentdata.colRow1Cat2
    ) {
      list = componentdata.colRow1Cat2.lines.map((component, index) => {
        return (
          <li key={index} className="list-inline-item">
            {this.cfp.getDataByKey(
              this.props.footerElement.data[0].messages,
              component
            )}
            
            
          </li>
        );
      });
      return <React.Fragment>{list}</React.Fragment>;
    }
  }
  siteLegal() {
    let list = null;
    let componentdata = this.props.footerElement.component.content;
    if (
      componentdata.hasOwnProperty("colRow2Cat2") &&
      componentdata.colRow2Cat2
    ) {
      list = componentdata.colRow2Cat2.list.map((component, index) => {
        return (
          
          
          <li key={index} className="list-inline-item ">
            <a className="btn-floating btn-sm btn-fb mx-1 footer-content">{component.text}</a>
          </li>
          
          
        );
      });
      return <React.Fragment>{list}</React.Fragment>;
    }
  }
  itemWraperSm() {
    let list = null;
    let componentdata = this.props.footerElement.component.content;
    if (
      componentdata.hasOwnProperty("colRow1Cat3") &&
      componentdata.colRow1Cat3
    ) {
      if (
        componentdata.colRow1Cat3.hasOwnProperty("socialMedia") &&
        componentdata.colRow1Cat3.socialMedia
      ) {
        list = componentdata.colRow1Cat3.socialMedia.map((component, index) => {
          return (
            <li key={index} className="list-inline-item">
              <a className="btn-floating btn-sm btn-fb ">
                <i className={component.icon} />
              </a>
            </li>
          );
        });
        return <React.Fragment>{list}</React.Fragment>;
      }
    }
  }
  itemWraper() {
    let list = null;

    let componentdata = this.props.footerElement.component.content;

    if (
      componentdata.hasOwnProperty("colRow1Cat1") &&
      componentdata.colRow1Cat1
    ) {
      list = componentdata.colRow1Cat1.map((component, index) => {
        return (
          // <li className="list-inline-item">
          //   <a href="#">
          //     {this.cfp.getDataByKey(
          //       this.props.footerElement.data[0].messages,
          //       component
          //     )}
          //   </a>
          // </li>
          <Col key={index} md="2">
            <h5 className=" mb-4 mt-3 font-footer-adjust ">
              {component.caption}
            </h5>
            <ul className="list-unstyled">
              {component.linkList.map((link, index) => {
                return (
                  <li key={index}>
                    <a href="#!">{link.listItem}</a>
                  </li>
                );
              })}
            </ul>
          </Col>
        );
      });
      return <React.Fragment>{list}</React.Fragment>;
    }
  }
  render() {
    return (
      <Footer
        color="stylish-color-dark"
        className="page-footer font-small pt-4 mt-4"
      >
        <Container fluid className="text-center text-md-left">
          <Row>
            <hr className="clearfix w-100 d-md-none" />
            {this.itemWraper()}
            <Col md="2">
              <h5 className=" mb-4 mt-3 font-footer-adjust ">
                {this.cfp.getDataByKey(
                  this.props.footerElement.data[0].messages,
                  this.props.footerElement.component.content.colRow1Cat2.caption
                )}
              </h5>
              <p>{this.blog()}</p>
              
              <a className="read-More">{this.cfp.getDataByKey(
                this.props.footerElement.data[0].messages,
                this.props.footerElement.component.content.colRow1Cat2.readMoreLink
              )}</a>
            </Col>
            <Col md="2">
              <h5 className=" mb-4 mt-3 font-footer-adjust ">
                {this.cfp.getDataByKey(
                  this.props.footerElement.data[0].messages,
                  this.props.footerElement.component.content.colRow1Cat3.caption
                )}
              </h5>
              <div className="">
                <ul className="list-unstyled list-inline">
                  {/* {this.itemWraperSm()} */}
                  <ul className="list-unstyled list-inline">{this.itemWraperSm()}</ul>
                </ul>
              </div>
            </Col>
          </Row>
        </Container>

        <hr />
        <div className="text-center">
          
        </div>
        <div className="footer-copyright text-center py-3">
          <Container fluid >
          <Row>
            <Col md="6" className="text-left footer-content">
            {this.cfp.getDataByKey(
              this.props.footerElement.data[0].messages,
              this.props.footerElement.component.content.colRow2Cat1.copyright
            )}
            </Col>
            <Col md="6" className="text-right">
              {this.siteLegal()}
            </Col>
          
            </Row>
          </Container>
          <Container />
        </div>
      </Footer>
    );
  }
}

export default FooterPage;



