import React, { PureComponent } from "react";
import ConfigProcessor from "../../ConfigJsonProcessor";
class HeaderProduct extends PureComponent {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  state = {};
  productlist() {
    if (
      this.props.product.product.hasOwnProperty(
        "ProductItems"
      ) &&
      this.props.product.product.ProductItems
    ) {
    let list = this.props.product.product.ProductItems.map((component, index) => {
      return (
        <div key={index} className="">
          <a className="" >
            <img
              className="" alt="productImg"
              src={this.cfp.getAssetUrl(
                this.props.product.assets,
                component.ProductItemImage
              )}
            />
            <p className="">
              {this.cfp.getDataByKey(
                this.props.product.data[0].messages,
                component.ProductTitle
              )}
            </p>
          </a>
        </div>
      );
    });
    return <React.Fragment>{list}</React.Fragment>;
  }
  }
  render() {
    return (
      <div className="slider-products">
        <div className="container">
          <div className="row seven-cols">{this.productlist()}</div>
        </div>
      </div>
    );
  }
}

export default HeaderProduct;



