import React, { Component } from "react";
import ConfigProcessor from "../ConfigJsonProcessor";
class Feature extends Component {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  state = {};
  itemWraper() {
    if (
      this.props.featureElement.component.content.hasOwnProperty("features")
    ) {
      const list = this.props.featureElement.component.content.features.map(
        (component, index) => {
          return (
            <div key={index} className="col-xs-12 col-sm-6 col-md-3 items upselling-img rec-images">
              <img alt="featureimage"
                src={this.cfp.getAssetUrl(
                  this.props.featureElement.assets,
                  component.assetId
                )}
              />
              <h4 className="heading-customer">
                {this.cfp.getDataByKey(
                  this.props.featureElement.data[0].messages,
                  component.title
                )}
              </h4>
              <p className="para-customer">
                {this.cfp.getDataByKey(
                  this.props.featureElement.data[0].messages,
                  component.content
                )}
              </p>
            </div>
          );
        }
      );
      return <React.Fragment>{list}</React.Fragment>;
    }
    const list = this.props.featureElement.component.content.features.map(
      (component, index) => {
        return (
          <div key={index} className="col-xs-12 col-sm-6 col-md-3 items upselling-img">
            <img alt="featureImg"
              src={this.cfp.getAssetUrl(
                this.props.featureElement.assets,
                component.assetId
              )}
            />
            <h4>
              {this.cfp.getDataByKey(
                this.props.featureElement.data[0].messages,
                component.title
              )}
            </h4>
            <p>
              {this.cfp.getDataByKey(
                this.props.featureElement.data[0].messages,
                component.content
              )}
            </p>
          </div>
        );
      }
    );
    return <React.Fragment>{list}</React.Fragment>;
  }
  render() {
    console.log("Feature props", this.props);
    return (
      <div className="upselling">
        <div className="container padding">
          <div className="row welcome text-center padding">
            <div className="col-12 title">
              <h2 className="display-5">
                {this.cfp.getDataByKey(
                  this.props.featureElement.data[0].messages,
                  this.props.featureElement.component.content.caption
                )}
              </h2>
              
            </div>
            {this.itemWraper()}
          </div>
        </div>
      </div>
    );
  }
}

export default Feature;



