import { PureComponent } from "react";
import Subscriber from "./subscriber";
class ConfigJSON extends PureComponent {
  constructor() {
    super();
  }
  state = {};

  async subcribe(page) {
    const subscriber = new Subscriber();
    if (page == "home") {
      let resp = await subscriber.getConfigJson().then(res => res.data);
      return resp;
    }
  }
}

export default ConfigJSON;



