import React, { Component } from "react";
import ConfigProcessor from "../ConfigJsonProcessor";
class RecommendedReading extends Component {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  state = {};
  itemWraper() {
    if (
      this.props.recreadElement.component.content.hasOwnProperty(
        "editorials"
      ) &&
      this.props.recreadElement.component.content.editorials
    ) {
      const list = this.props.recreadElement.component.content.editorials.map(
        (component, index) => {
          return (
            <div key={index} className="col-xs-12 col-sm-6 col-md-3 items upselling-img">
              <img alt="recreading-Img"
                src={this.cfp.getAssetUrl(
                  this.props.recreadElement.assets,
                  component.assetId
                )}
              />
              <h4 className="date-label">{component.date}</h4>
              <h6 className="reading-heading">
                {this.cfp.getDataByKey(
                  this.props.recreadElement.data[0].messages,
                  component.title
                )}
              </h6>
              <p className="reading-para">
                {this.cfp.getDataByKey(
                  this.props.recreadElement.data[0].messages,
                  component.content
                )}
                
                <a className="readmore-upselling">
                {this.cfp.getDataByKey(
                  this.props.recreadElement.data[0].messages,
                  component.recReadmorlnk
                )}</a>
              </p>
            </div>
          );
        }
      );
      return <React.Fragment>{list}</React.Fragment>;
    }
  }
  render() {
    console.log("upselling", this.props);
    return (
      <div className="upselling">
        <div className="container padding">
          <div className="row welcome text-center padding">
            <div className="col-12 title">
              <h2 className="display-5">
                {this.cfp.getDataByKey(
                  this.props.recreadElement.data[0].messages,
                  this.props.recreadElement.component.content.caption
                )}
              </h2>
              <h3 className="reading-subtitle">
                {this.cfp.getDataByKey(
                  this.props.recreadElement.data[0].messages,
                  this.props.recreadElement.component.content.subCaption
                )}
              </h3>
            </div>
            {this.itemWraper()}
          </div>
        </div>
      </div>
    );
  }
}

export default RecommendedReading;



