import axios from "axios";

export default class Subscriber {
  constructor() {
    this.sessionToken = "";
    this.configSessionApi = {
      url: `http://103.79.223.62:8080/ce-0.0.1/ce/v1/rocketship/partners/application/session`,
      method: "post",
      headers: {
        "Content-Type": "application/json"
      },
      data: {
        appKey: "x123",
        deviceKey: "my firsr profile"
      }
    };
  }
  getSessionAPi() {
    return axios
      .request(this.configSessionApi)
      .then(res => {
        this.sessionToken = res.accessToken;
        return res;
      })
      .catch(error => {
        return error;
      });
  }

  sendRequest(response, config) {
    return axios
      .request(this.configJSONApi)
      .then(res => {
        return res;
      })
      .catch(error => {
        return error;
      });
  }

  async getConfigJson() {
    // if (!this.sessionToken) {
    //   this.getSessionAPi();
    // } else {
    const sessionApi = await this.getSessionAPi();
    const {
      data: { accessToken }
    } = sessionApi;
    this.sessionToken = accessToken || "";
    if (this.sessionToken !== "") {
      this.configJSONApi = {
        url:
          "http://103.79.223.62:8080/ce-0.0.1/ce/v1/rocketship/partners/aaaa/applications/web/pages/page_0001",
        method: "get",
        mode: "no-cors",
        withCredentials: true,
        headers: {
          // "Access-Control-sAllow-Origin": "*",
          "Content-Type": "application/json",
          Accept: "application/json",
          "x-auth-token": this.sessionToken
        },
        credentials: "same-origin",
        crossdomain: true
      };
      return await this.sendRequest(sessionApi, this.configJSONApi);
    }
  }
}



