import React, { Component } from "react";
// import Footer from "../components-library/footer_template1";
import RecommendedReading from "../components-library/recreading-section";
import Feature from "../components-library/feature-section";
import FooterPage from "../components-library/footer";

import HeaderWraper from "../components-library/headercomponents/header-wraper";
import Protection from "../components-library/protection";

//import HeaderPlanWraper from "../components-library/headercomponents/header-plan-wraper";
class TemplateMaper extends Component {
  state = {};
  // constructor() {
  //   super();
  // }
  matchTemplate(element) {
    console.log("element tempId", element.component.templateId);
    switch (element.component.templateId) {
      case "header": 
         return <HeaderWraper key={element.component.templateId} header={element} />;
        // case "plan":
        // return <HeaderPlanWraper header={element} />;
        // break;
      case "footer":
        return <FooterPage key={element.component.templateId} footerElement={element} />;
      case "feature":
        return <Feature key={element.component.templateId} featureElement={element} />;
      case "protection":
        return <Protection key={element.component.templateId} protectionElement={element} />;
      case "recommendedreading":
        return <RecommendedReading key={element.component.templateId} recreadElement={element} />;
      // case "upselling":
      //   return <Upselling upselling={element} />;
      //   break;
      // case "policydetail":
      //   return <PolicyDetail policydetail={element} />;
      //   break;
      // case "policystatus":
      //   return <PolicyStatus policystatus={element} />;
      //   break;
      // case "enquire":
      //   return <EnquireSection enquire={element} />;
      //   break;
      // case "product":
      //   return <HeaderProductWraper header={element} />;
      //   break;
      // case "quote":
      //   return <HeaderQuoteWraper header={element} />;
      //   break;
      // case "benifit":
      //   return <PolicyBenifit benifit={element} />;
      //   break;
      // case "information":
      //   return <PolicyInformation information={element} />;
      //   break;
      default:
        return null;
    }
  }
}

export default TemplateMaper;



