import React, { PureComponent } from "react";
import HeaderNavWraperLeft from "./header-nav-left";
import ConfigProcessor from "../../../ConfigJsonProcessor";
class HeaderNavWraper extends PureComponent {
  constructor(props) {
    super(props);
    this.cfp = new ConfigProcessor();
  }
  state = {};
  render() {
    return (
      <nav className="navbar navbar-expand-lg navbar-light fixed-top">
        <div className="container-fluid">
          <h1>
            <a className="navbar-brand">
              <img alt="tuneProtectLogo"
                src={this.cfp.getAssetUrl(
                  this.props.navigation.assets,
                  this.props.navigation.navigation.TPLogo
                )}
              />
            </a>
          </h1>
          {/* <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarResponsive"
          >
            <span className="navbar-toggler-icon" />
          </button> */}
          <HeaderNavWraperLeft menubar={this.props.navigation} />
        </div>
      </nav>
    );
  }
}

export default HeaderNavWraper;



